@import url(partials/reset.css);

@import url(partials/fonts.css);

@import url(partials/fullpage.css);

/* my style */

a {
  text-decoration: none;
}

body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-size: 1vw;
}

.container {
  margin: 0 auto;
  position: relative;
}

header {
  height: 3.64583vw;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

header .flex-block {
  padding: 0 1.71875vw 0 1.5625vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  height: 100%;
}

header .flex-block .yana-logo {
  width: 10.9375vw;
}

header .flex-block .yana-logo img {
  display: block;
  width: 100%;
}

header .flex-block .super-logo {
  width: 6.30208vw;
}

header .flex-block .super-logo img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

header .flex-block .mobile-menu {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 7.1875vw;
  height: 5vw;
  margin-left: -3.75vw;
  margin-top: -2.5vw;
  cursor: pointer;
  display: none;
}

header .flex-block .mobile-menu svg {
  width: 100%;
  height: 100%;
  display: block;
}

header .flex-block .mobile-menu svg rect {
  position: relative;
  transition: 0.3s ease;
}

header .flex-block .mobile-menu svg rect:nth-child(1) {
  transform-origin: 50% 50%;
}

header .flex-block .mobile-menu svg rect:nth-child(3) {
  transform-origin: 50% 50%;
}

header .flex-block .mobile-menu.active svg rect:nth-child(1) {
  transform: rotate(45deg) translateY(45%);
}

header .flex-block .mobile-menu.active svg rect:nth-child(2) {
  opacity: 0;
}

header .flex-block .mobile-menu.active svg rect:nth-child(3) {
  transform: rotate(-45deg) translateY(-45%);
}

footer {
  height: 5.88542vw;
  background: #000000;
}

footer .flex-block {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2.91667vw 0 2.13542vw;
  box-sizing: border-box;
}

footer .flex-block .social-block {
  width: 9.375vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

footer .flex-block .social-block .social-link {
  display: block;
  cursor: pointer;
  width: 1.875vw;
  height: 1.875vw;
}

footer .flex-block .social-block .social-link svg {
  width: 100%;
  display: block;
}

footer .flex-block .social-block .social-link:hover svg path {
  fill: #E9B382;
}

footer .flex-block .footer-menu {
  display: flex;
  align-items: center;
  margin-left: 10.88542vw;
}

footer .flex-block .footer-menu a {
  font-size: 0.88542vw;
  color: #ffffff;
  text-transform: uppercase;
}

footer .flex-block .footer-menu a + a {
  margin-left: 2.96875vw;
}

footer .flex-block .footer-menu a:hover {
  color: #E9B382;
}

footer .flex-block .copy {
  font-size: 0.83333vw;
  color: #FFFFFF;
  opacity: 0.6;
  text-align: center;
}

footer .flex-block .copy br {
  display: none;
}

@media only screen and (max-width: 1024px) {
  header {
    height: 14.375vw;
  }

  header .flex-block {
    padding: 0 1.25vw 0 4.0625vw;
  }

  header .flex-block .yana-logo {
    width: 28.75vw;
  }

  header .flex-block .super-logo {
    width: 25vw;
  }

  header .flex-block .mobile-menu {
    display: block;
  }

  footer {
    height: 65.3125vw;
  }

  footer .flex-block {
    flex-direction: column;
  }

  footer .flex-block {
    padding: 11.5625vw 3.125vw 17.1875vw;
  }

  footer .flex-block .social-block {
    width: 56.25vw;
  }

  footer .flex-block .social-block .social-link {
    width: 11.25vw;
  }

  footer .flex-block .social-block .social-link {
    height: 11.25vw;
  }

  footer .flex-block .footer-menu {
    display: none;
  }

  footer .flex-block .copy {
    font-size: 4.375vw;
  }

  footer .flex-block .copy {
    line-height: 150%;
  }

  footer .flex-block .copy br {
    display: initial;
  }
}